.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  margin: 0;
  padding: 32px 16px 16px;
  display: flex;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  align-items: center;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-price {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__buy {
  text-align: center;
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__iMac {
  width: 160px;
  height: 134px;
  margin: 0 auto;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  display: inline-block;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  font-family: Roboto, sans-serif;
  display: flex;
}
